      <div>
        <el-button :disabled="learn_able" @click="Next()" type="success">Next</el-button>
      </div>
    </section>
    <section>
      <img src="@/assets/UVlearn.png">
      <p>Ultraviolet is a type of light. We also often call it "UV light" for short. It is special for two reasons. First, we humans cannot see it. Second, it can darken and burn our skins if it is very strong and shine on our skins for long. There are much ultraviolet light in natural sunlight. That is why it is best to apply sunblocks when going under the sun.</p>
      <el-button @click="Jump(3,0)">Done</el-button>
    </section>
    <section>
      <img id="lux_img" src="@/assets/LUXlearn.png">
      <p>The unit of measurement for light is known as <b>luminous flux</b> or <b>Lux</b>, which decreases and increases depending on the distance from the source of light. </p>
      <p>Generally speaking, 0 lux means no light at all, 400 lux is close to the light at sunrise and sunset. The light intensity of a clear day is usually above 10,000 lux</p>
