<script>
  import Reveal from 'reveal.js/js/reveal'

  export default {
    props: ['learn_radio'],
    data() {
      return{
        learn_text: 'Here are some information to refresh your knowledge',
        learn_able: false,
      };
    },
    methods: {
      Next() {
        Reveal.right();
      },
      Jump(h,v) {
