      },
      learnCheck() {
        this.learn_able = false;
      },
    },
    watch: {
      learn_radio() {
        if (this.learn_radio < 4) {
          this.learn_text = 'Please read these three document before start your experiment';
          this.learn_able = true;
        }
      }
    }
  }
