<template>
  <div>
    <p v-if="count != 0">Great! You've collected {{count}} piece of data!</p>
    <p v-if="count == 4">Go to see your result!</p>
    <p v-if="uvData && !able">Now, let's capture data for <i>{{material}}</i></p>
    <p v-if="uvData">{{time}} </p>
    <img id="zally" :src="zally_face">
    <el-button @click="capture()" :disabled="able">Capture Data</el-button>
  </div>
</template>

<script>
  import happy from '@/assets/HappyZally.gif'
  import sad from '@/assets/SadZally.gif'
  import sunburnt from '@/assets/SunburntZally.gif'
  import connect from '@/assets/ZallyConnect.png'

  function sleep(milliSeconds){ 
    var StartTime =new Date().getTime(); 
    let i = 0;
    while (new Date().getTime() <StartTime+milliSeconds);

}

  export default {
    props: ['checkList', 'uvData', 'luxData'],
    data() {
      return {
        radio: '',
        flag: true,
        count: 0,
        zally_face: connect,
        zally_url: '',
        able: true,
        cap_data: [],
        material: 'No Cover',
        time: 'Ready? Click Capture Data!',
        totaltime: 3,
      };
    },
    watch: {
      uvData() {
        if (this.uvData > 1.5) {
          this.zally_face = sunburnt;
          this.zally_url = require('@/assets/sunburnt.png')
        } else if (this.luxData < 2000) {
          this.zally_face = sad;
          this.zally_url = require('@/assets/sad.png')
        } else {
          this.zally_face = happy;
          this.zally_url = require('@/assets/happy.png')
        }
        if (this.uvData != null && this.flag == true) {
          this.able = false;
        }
      },
      count() {
        if (this.count == 4) {
          this.$emit('resultData', this.cap_data);
          this.able = true;
        }
      }
    },
    methods: {
      capture() {
        this.able = true;
        this.flag = false;
        this.time = this.totaltime;
        let clock = window.setInterval(() => {
          this.totaltime--
          this.time = this.totaltime
          if (this.totaltime < 1) {
            this.time = "Done!"
          }
          if (this.totaltime < 0) {
            window.clearInterval(clock)
            this.time = ' '
            this.able = false
            this.cap_data.push({material: this.material,uv: this.uvData, lux: this.luxData, url: this.zally_url})
            this.material = this.checkList[this.count];
            this.count += 1;
            this.totaltime = 3;
          }
        },1000)
      },
    }
  }
</script>

<style>
  .cap_button {
    display: block;
    margin-top: 6px;
    max-width: 200px;
    margin-left: 4px !important;
  }
  #zally {
    height: 30vh;
    border: 0px;
    box-shadow: none;
  }
  .Done {
    pointer-events: none;
  }
</style>