<template>
    <div>

  <div id="uvchart">
    <div class="card front" id="card1">
      <div class="data">
        <p class="title">UV Index</p>
        <p class="number">{{uvData | optimize}}</p>
      </div>
      <div class="apex">
        <apexchart type="line" height="300" ref="uvChart" :options="uvchartOptions" :series="uvSeries"></apexchart>
      </div>
    </div>
    <div class="card back">
      <div class="data">
        <p class="title">LUX</p>
        <p class="number">{{luxData}}</p>
      </div>
      <div class="apex">
        <apexchart type="line" height="300" ref="luxChart" :options="luxchartOptions" :series="luxSeries"></apexchart>
      </div>
    </div>
  </div>
    <el-button @click="flip()">{{text}}</el-button>
  </div>
</template>

<script>
  import VueApexCharts from 'vue-apexcharts'

  let uvseries = ['0'];
  let luxseries = ['0'];

  function getNewSeries(series, uv) {
    series.push(uv);
  }

  function luxgetNewSeries(series, baseval, lux) {
    var luxnewDate = baseval + 1;
    luxlastDate = luxnewDate;
    series.push({
      x: luxnewDate,
      y: lux
    });
  }


	export default {
		props: ['uvData', 'luxData', 'count'],
    components: {
      'apexchart': VueApexCharts,
    },
		data() {
      return{
        uvD: null,
        luxD: null,
        luxM: 0,
        text: 'Check Lux',
        uvSeries: [{
          data: uvseries.slice()
        }],
        luxSeries: [{
          data: luxseries.slice()
        }],
        uvchartOptions: {
          chart: {
            id: 'realtime',
            height: 300,
            type: 'line',
            animations: {
              enabled: true,
              easing: 'linear',
              dynamicAnimation: {
                speed: 500
              }
            },
            toolbar: {
              show: false
            },
            zoom: {
              enabled: false
            }
          },
          // colors: ['#F7F8F9'],
          grid: {
            row: {
              colors:['#A465AA','#A465AA','#A465AA','#EE3A12','#EE3A12','#F68501','#FCF302','#FCF302','#41AB24','#41AB24'],
              opacity: 0.2
            }
          },
          dataLabels: {
            enabled: false
          },
          stroke: {
            curve: 'smooth'
          },
          title: {
            enabled: false
          },
          markers: {
            size: 0
          },
          xaxis: {
            range: 20,
            labels: {
              show: false
            }
          },
          yaxis: {
            max: 15,
            show: false
          },
          legend: {
            show: false
          },
        },
        luxchartOptions: {
          chart: {
            id: 'realtime',
            height: 300,
            type: 'line',
            animations: {
              enabled: true,
              easing: 'linear',
              dynamicAnimation: {
                speed: 500
              }
            },
            toolbar: {
              show: false
            },
            zoom: {
              enabled: false
            }
          },
          dataLabels: {
            enabled: false
          },
          stroke: {
            curve: 'smooth'
          },
          title: {
            enabled: false
          },
          markers: {
            size: 0
          },
          xaxis: {
            range: 20,
            labels: {
              show: false
            }
          },
          yaxis: {
            show: false
          },
          legend: {
            show: false
          },
        },
      }
		},
    watch: {
      count() {
        console.log(this.count)
        this.uvD = this.uvData;
        if (this.uvD != null && this.uvD != '' && this.uvD < 20) {
          getNewSeries(uvseries, this.uvD)
          this.$refs.uvChart.updateSeries([{
            data: uvseries
          }])
        }
        this.luxD = this.luxData;
        if (this.luxD != null && this.luxD != '') {
          getNewSeries(luxseries, this.luxD)
          this.$refs.luxChart.updateSeries([{
            data: luxseries
          }])
        }
      }
    },
    methods: {
      flip() {
        let card = document.getElementById('uvchart');
        card.classList.toggle('flipped');
        if (this.text == "Check Lux") {
          this.text = "Check UV Index";
        } else {
          this.text = "Check Lux";
        }
      }
    },
    mounted() {
    },
    filters: {
      optimize: function(value) {
        if (value > 15) {
          value = 0;
        }
        return value;
      }
    }
	}
</script>

