<script>
  import Reveal from 'reveal.js/js/reveal'
  import dataViz from '@/components/dataViz_uv'
  import uv from './Measure_uv'
  import dataCap from '@/components/dataCapture_uv'
  import learn from '@/components/Learning_uv'

  export default{
    data() {
      return {
        learn_radio: '',
        materiallist:['Baking Paper', 'Red Paper', 'Clear Plastic', 'Paper Napkin', 'Green Cellophane', 'Clear Plastic + Sunblock'],
        checklist: [],
        radio: '',
        low_board: 'low protection',
        medium_board: 'medium protection',
        high_board: 'high protection',
        input_material: '',
        input_reason: '',
        input_number: '',
        warning_text: '',
        input_evidence: '',
        input_finding: '',
        uvData: null,
        luxData: null,
        reData: null,
        add_visible: false,
        multi_able: true,
        radio_able: true,
        next_able: true,
        learn_able: false,
        number_able: true,
        number_wrong: false,
        number_success: false,
        over_able: true,
        hypo_result: '',
        report: '',
      }
    },
    components: {
      'dataViz': dataViz,
      'uv': uv,
      'dataCap': dataCap,
      'learn': learn,
    },
    mounted() {
      Reveal.initialize({
        // keyboard: false,
        controls: false,
        overview: true,
        touch: false,
        progress: false
      });
      Reveal.addEventListener('hypo1', function() {
        console.log('here');
      });
    },
    methods: {
      Next() {
        Reveal.right();
      },
      Left() {
        Reveal.left();
      },
      Down() {
        Reveal.down();
      },
      Jump(h,v) {
        Reveal.slide(h,v);
      },
      addMaterial() {
        if (this.input_material != '') {
          this.materiallist.push(this.input_material);
        }
        this.add_visible = false;
      },
      dragStart: e => {
        const target = e.target;
        const dragFlag = 0;
        e.dataTransfer.setData('card_id', target.id);
      },
      drop(e) {
        const card_id = e.dataTransfer.getData('card_id');
        const card = document.getElementById(card_id);
        card.style.display = "none";
        card.style.margin = '0';
        e.target.appendChild(card);
        card.style.display = "block";
        if (e.target.id == "high") {
          this.radio = card_id;
        }
      },
      Show() {
        if (this.input_number == '1.5') {
          this.number_success = true;
        } else {
          this.number_wrong = true;
        }
        this.number_able = false;
      },
      checkInput() {
        let flag = 0;
        const text = this.input_reason;
        const warn = document.getElementById('warning');
        const space = text.length - text.replace(/\s/g, "").length
        if (text.length < 10) {
          flag = 1;
          this.warning_text = 'Oh this answer is too short! Could you try to write more?'
          warn.style.display = 'block';
        } else if (space < 2) {
          flag = 1;
          this.warning_text = 'Oh this answer seems a little hard to understand. Could you please explain it?'
          warn.style.display = 'block';
        }
        if (flag == 0) {
          warn.style.display = 'none';
          Reveal.right();
        }
      },
      muvData(uv) {
        this.uvData = uv;
      },
      mluxData(lux) {
        this.luxData = lux;
      },
      resultData(rd) {
        this.reData = rd;
      },
      Export() {
        window.print();
      },
      Redo() {
        location.reload();
      }
    },
    watch: {
      learn_radio() {
        if (this.learn_radio < 4) {
          Reveal.right();
        } else {
          this.Jump(4,0);
        }
      },
      reData() {
        if (this.reData != null) {
          this.next_able = false;
        }
      },
      checklist() {
        if (this.checklist.length == 3) {
          this.multi_able = false;
        }
      },
      radio() {
        if (this.radio != '') {
          this.radio_able = false;
        }
      },
      hypo_result() {
        if (this.hypo_result == "N") {
          this.report = "didn't";
        }
      },
      input_finding() {
        this.over_able = false;
      }
    }
  }
</script>

