<template>
  <div class="about">
    <getSerial @nuvData = "nuvData" @nluxData = "nluxData" @ncount = "ncount"></getSerial>
    <dataViz :uvData = "uvData" :luxData = "luxData" :count = "count"></dataViz>
  </div>
</template>

<script>
	import getSerial from '@/components/getSerial'
  import dataViz from '@/components/dataViz_uv'
  export default {
  name: 'uv',
  data() {
    return {
      uvData: null,
      luxData: null,
      count: 0,
      allData: null,
    }
  },
  components: {
    'getSerial': getSerial,
    'dataViz': dataViz,
  },
  methods: {
    nuvData(uv) {
      this.uvData = uv;
      this.$emit('muvData', uv);
    },
    nluxData(lux) {
      this.luxData = lux;
      this.$emit('mluxData', lux);
    },
    ncount(count) {
      this.count = count;
    }
  }
}
</script>

